@font-face {
font-family: Rubik;
src: local("Rubik"),
    url(/IIScCodingTask/static/media/Rubik-Medium.6fa3da85.ttf) format("truetype");
}

@font-face {
font-family: Rubik-Light;
src: local("Rubik-Light"),
    url(/IIScCodingTask/static/media/Rubik-Light.a511edd8.ttf) format("truetype");
}

.page-container-PhoneAuth{
    display: flex;
    flex-direction: row;
    height: 88vh;
    padding: 5px;
}

.container-left-PhoneAuth{
    display: flex;
    flex-direction: column;
    flex: 0.25 1;
    min-width: 250px;
    padding: 5px;
}

.pageTitle-PhoneAuth{
    font-family: "Rubik", sans-serif;
    font-size: 40px;
    margin-bottom: 20vh;
}

.pageBody-PhoneAuth{
    font-family: "Rubik-Light", sans-serif;
    font-size: 25px;
}

.container-right-PhoneAuth{
    display: flex;
    flex: 1 1;  
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: #f7f7f7;
}

.g-recaptcha {
    transform:scale(0.94);
    -webkit-transform:scal(0.94);
    transform-origin:0 0;
    -webkit-transform-origin:0 0;
} 

@media only screen and (max-width: 800px) {
    .page-container-PhoneAuth{
        flex-direction: column;
    }
}
